@charset "UTF-8";
@font-face {
  font-family: "pt-sans-narrow-v11-latin_cyrillic-700";
  src: url("../fonts/pt-sans-narrow-v11-latin_cyrillic-700.woff2") format("woff2"), url("../pt-sans-narrow-v11-latin_cyrillic-700.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "pt-sans-narrow-v11-latin_cyrillic-regular";
  src: url("../fonts/pt-sans-narrow-v11-latin_cyrillic-regular.woff2") format("woff2"), url("../pt-sans-narrow-v11-latin_cyrillic-regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

*, *:before, *:after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

body {
  margin: 0;
  font-family: "PT Sans Narrow", Arial, sans-serif;
  font-size: 14px;
  line-height: 24px;
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
  background-color: #000000;
  background-image: url("../img/bg.jpg");
  background-position: top center;
  background-repeat: no-repeat;
}

.container {
  max-width: 960px;
  margin: 0 auto;
  padding: 0 10px;
}

.title {
  font-size: 30px;
  font-weight: 42px;
  margin: 0;
  margin-bottom: 25px;
}

.header {
  position: relative;
  margin-bottom: 70px;
}

.header__nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
  background-color: transparent;
}

.header__logo {
  order: 2;
}

.header__site-navigation {
  display: flex;
  flex-wrap: wrap;
  flex-grow: 1;
}

.header__user-navigation {
  display: flex;
  max-width: 140px;
}

.site-navigation,
.user-navigation {
  list-style: none;
  padding: 0;
  margin: 0;
}

.site-navigation__link,
.user-navigation__link {
  display: block;
  padding: 25px 20px;
  text-decoration: none;
  color: #ffffff;
}

.site-navigation a:hover,
.site-navigation a:focus,
.user-navigation a:hover,
.user-navigation a:focus {
  background-color: #242424;
}

.logo {
  width: 368px;
  height: 153px;
}

.nav-wrap {
  width: 100%;
  margin-bottom: 60px;
  background-color: #000000;
}

.nav-wrap .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.user-navigation__link--login {
  position: relative;
  padding-left: 50px;
}

.user-navigation__link--login::before {
  content: "";
  position: absolute;
  top: 27px;
  left: 24px;
  width: 14px;
  height: 14px;
  background: url("../img/login.svg"), no-repeat, 0 0;
  opacity: 0.3;
}

.user-navigation__link--login:hover::before {
  opacity: 1;
}

.features {
  margin-bottom: 80px;
}

.features__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: space-between;
}

@media (max-width: 860px) {
  .features__list {
    flex-direction: column;
    align-items: center;
  }
}

.features__item {
  width: 33.3333%;
  text-align: center;
}

@media (max-width: 860px) {
  .features__item {
    width: auto;
    margin-bottom: 10px;
  }
}

.features__title {
  position: relative;
  margin: 0;
  margin-bottom: 60px;
  font-size: 30px;
  line-height: 42px;
}

.features__title::after {
  content: "";
  position: absolute;
  bottom: -30px;
  right: 50%;
  width: 20px;
  height: 20px;
  margin-right: -10px;
  background-color: #ffffff;
  transform: rotate(45deg);
}

.features__text {
  margin: 0 10px;
}

.section-wrapper {
  display: flex;
  justify-content: space-between;
  padding: 50px 80px;
  margin-bottom: 35px;
  color: #000000;
  background-color: #f6f3f0;
  background-image: url("../img/line.png"), url("../img/section-pattern.jpg");
  background-position: center, 0, 0;
  background-repeat: no-repeat, repeat;
}

@media (max-width: 900px) {
  .section-wrapper {
    flex-direction: column;
    background-image: url("../img/section-pattern.jpg");
    background-position: center;
    background-repeat: repeat;
    padding: 25px 40px;
  }
}

@media (max-width: 390px) {
  .section-wrapper {
    padding: 25px 10px;
  }
}

.news {
  width: 380px;
}

.news__list {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-bottom: 25px;
}

.news__item {
  margin-bottom: 25px;
}

.news__text {
  margin: 0;
}

.news__date {
  text-transform: none;
}

@media (max-width: 900px) {
  .news {
    width: 100%;
    margin-bottom: 50px;
    text-align: center;
  }
}

.gallery {
  display: flex;
  flex-direction: column;
  width: 300px;
  text-align: center;
}

.gallery__btn {
  width: 140px;
  margin: 0;
}

@media (max-width: 600px) {
  .gallery__btn {
    width: auto;
  }
  .gallery__btn--back {
    margin-right: 10px;
  }
}

@media (max-width: 340px) {
  .gallery {
    width: 100%;
  }
}

.slider-gallery__item img {
  width: 286px;
  height: 164px;
}

.gallery-btn-wrapper {
  margin-top: auto;
  display: flex;
  justify-content: space-between;
}

@media (max-width: 600px) {
  .gallery-btn-wrapper {
    justify-content: center;
    align-items: center;
  }
}

.contacts {
  width: 380px;
}

.contacts__text {
  margin: 0;
  margin-bottom: 25px;
}

.contacts__btn:not(:last-child) {
  margin-right: 16px;
}

@media (max-width: 900px) {
  .contacts {
    width: 100%;
    margin-bottom: 50px;
    text-align: center;
  }
  .contacts__title {
    line-height: 1.5;
  }
}

@media (max-width: 430px) {
  .contacts__btn {
    margin-right: 0;
    margin-bottom: 10px;
  }
}

.booking {
  width: 300px;
}

.booking__form {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.booking__item {
  width: 140px;
  margin: 0;
  margin-bottom: 10px;
}

.booking__item label {
  display: block;
  margin-left: 15px;
  margin-bottom: 10px;
}

.booking__item input {
  width: 140px;
  padding: 10px 15px;
  font: inherit;
  background-color: transparent;
  border: 2px solid #000000;
}

.booking__item input:focus {
  border-color: #663d15;
}

.booking__item input[type=date] {
  padding-right: 5px;
}

.booking__btn {
  width: 100%;
  margin-top: 10px;
  margin-right: 0;
}

@media (max-width: 900px) {
  .booking {
    width: 100%;
    margin-bottom: 50px;
    text-align: center;
  }
}

.footer {
  margin-top: 65px;
  padding: 60px 0;
  background-color: #222222;
  background-image: url("../img/footer-patern.png");
  background-position: 0 0;
  background-repeat: repeat;
}

.footer__contacts {
  width: 320px;
  margin: 0;
  margin-right: 80px;
}

.footer__contacts a {
  color: #ffffff;
  text-decoration: underline;
}

.footer__contacts a:hover, .footer__contacts a:focus {
  text-decoration: none;
}

@media (max-width: 560px) {
  .footer__contacts {
    text-align: center;
    margin-right: 0;
    margin-bottom: 10px;
  }
}

.footer__socials p {
  padding: 0;
  margin: 0;
  margin-bottom: 15px;
}

.footer .container {
  display: flex;
  justify-content: space-between;
}

@media (max-width: 560px) {
  .footer .container {
    flex-direction: column;
    align-items: center;
  }
}

.socials {
  width: 140px;
  text-align: center;
}

.socials__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 auto;
  padding: 0;
  list-style: none;
}

.socials__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 43px;
  height: 43px;
  background-color: #000000;
}

.socials__link:hover, .socials__link:focus {
  background-color: #ffffff;
}

.socials__link:hover path,
.socials__link:focus path {
  fill: #000000;
}

.swiper-button-prev,
.swiper-button-next,
.btn {
  display: inline-block;
  border: none;
  cursor: pointer;
  font: inherit;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  vertical-align: middle;
  padding: 10px 30px;
  color: #ffffff;
  background-color: #000000;
}

.btn-back {
  margin-right: 16px;
}

.btn:hover,
.btn:focus,
.btn:active {
  background-color: #663d15;
}

.btn.disabled,
.btn:disabled {
  cursor: default;
  opacity: 0.5;
  background-color: #000000;
}

.menu-icon {
  z-index: 19;
  position: relative;
  display: none;
  width: 30px;
  height: 30px;
  cursor: pointer;
}

/* Условие для экранов с шириной от 0 до 1200px */
@media (max-width: 769px) {
  .menu-icon {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .header__site-navigation {
    display: block;
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100vh;
    padding-left: 40px;
    text-align: left;
    background-color: #000000ef;
  }
  .header__site-navigation--active {
    left: 0;
  }
}

.menu-icon-line {
  position: relative;
  width: 30px;
  height: 5px;
  background-color: #fff;
}

.menu-icon-line::before {
  position: absolute;
  left: 0;
  top: -10px;
  content: '';
  width: 30px;
  height: 5px;
  background-color: #fff;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
}

.menu-icon-line::after {
  position: absolute;
  left: 0;
  top: 10px;
  content: '';
  width: 30px;
  height: 5px;
  background-color: #fff;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
}

.menu-icon.menu-icon-active .menu-icon-line {
  background-color: transparent;
}

.menu-icon.menu-icon-active .menu-icon-line::before {
  transform: rotate(45deg);
  top: 0;
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
}

.menu-icon.menu-icon-active .menu-icon-line::after {
  transform: rotate(-45deg);
  top: 0;
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
}

